
import { onMounted, onUnmounted, reactive, ref, toRefs } from 'vue';
import Bar from './Bar.vue';
import { addResizeListener, removeResizeListener } from './resize-event';

export default {
  name: 'c-scrollbar',
  components: {
    Bar,
  },
  props: {
    width: {
      type: String,
      default: '',
    },
    height: {
      type: String,
      default: '',
    },
    maxHeight: {
      type: String,
      default: '',
    },
    maxWidth: {
      type: String,
      default: '',
    },
    trigger: {
      type: String,
      default: 'always', // hover é¼ æ ç§»å¨ä¸å»æ¾ç¤º always ä¸ç´æ¾ç¤º none ä¸æ¾ç¤º
    },
    noresize: Boolean, // å¦æ container å°ºå¯¸ä¸ä¼åçååï¼æå¥½è®¾ç½®å®å¯ä»¥ä¼åæ§è½
    direction: {
      type: String,
      default: 'all', // all æ¨ªå çºµå é½åºç°æ»å¨æ¡ x æ°´å¹³ y åç´
    },
    vBarStyle: Object,
    hBarStyle: Object,
    vThumbStyle: Object,
    hThumbStyle: Object,
  },
  setup(props: any, { emit }: any) {
    const wrapRef = ref();
    const state = reactive({
      vThumbHeight: '0',
      hThumbWidth: '0',
      moveX: '0%',
      moveY: '0%',
      hover: false,
    });

    /**
     * çå®æ»å¨æ¡æ»å¨æ¶ï¼ä¿®æ¹æ»å¨æ¡barç§»å¨è·ç¦»
     */
    function handleScroll(event: Event) {
      const { scrollTop, clientHeight, scrollLeft, clientWidth }: any = event.target;
      state.moveY = `${(scrollTop * 100) / clientHeight}%`;
      state.moveX = `${(scrollLeft * 100) / clientWidth}%`;
      emit('scroll', event);
    }

    /**
     * æ´æ°thumbçå®½é« å¤å±å®¹å¨é«åº¦ / æ´ä¸ªåå®¹çé«åº¦ï¼æ´ä¸ªå¯æ»å¨åå®¹åºåï¼ =  æ»å¨æ¡thumbçé«åº¦ / æ»å¨è½¨éçé«åº¦ï¼ä¸è¬æ»å¨è½¨éçé«åº¦ä¸å¤å±å®¹å¨ç¸ç­ï¼
     */
    const hasVBar = ref(false);
    const hasHBar = ref(false);
    function update() {
      if (wrapRef.value) {
        const heightPercentage = (wrapRef.value.clientHeight * 100) / wrapRef.value.scrollHeight;
        const widthPercentage = (wrapRef.value.clientWidth * 100) / wrapRef.value.scrollWidth;

        if (heightPercentage < 100) {
          hasVBar.value = true;
        } else {
          hasVBar.value = false;
        }

        if (widthPercentage < 100) {
          hasHBar.value = true;
        } else {
          hasHBar.value = false;
        }
        state.vThumbHeight = heightPercentage < 100 ? `${heightPercentage}%` : '';
        state.hThumbWidth = widthPercentage < 100 ? `${widthPercentage}%` : '';
      }
    }

    function setScrollTop(value: number) {
      wrapRef.value.scrollTop = value;
    }

    function setScrollLeft(value: number) {
      wrapRef.value.scrollLeft = value;
    }

    onMounted(() => {
      update(); // åå§åè°ç¨ä¸æ¬¡ï¼è®¡ç®æ»å¨æ¡é»è®¤é«åº¦
      if (!props.noresize) {
        addResizeListener(wrapRef.value, update); // çå¬åç´ ååï¼å¦æå®¹å¨DOMååè§¦åæ´æ°
      }
    });

    onUnmounted(() => {
      if (!props.noresize) {
        removeResizeListener(wrapRef.value, update);
      }
    });

    return {
      ...toRefs(state),
      wrapRef,
      handleScroll,
      hasVBar,
      hasHBar,
      setScrollTop,
      setScrollLeft,
    };
  },
};
