<template>
  <div :class="`c-scrollbar-bar c-scrollbar-${direction}`"
       @mousedown="handleBarMouseDown"
       :style="barStyle"
       ref="barRef">
    <div class="c-scrollbar-thumb"
         ref="thumbRef"
         @mousedown="handleThumbMouseDown"
         :style="thumbStyleObj"></div>
  </div>
</template>

<script lang="ts">
import { computed, ComputedRef, ref } from 'vue';
import { BAR_MAP } from './utils';
import { BarMap, BarMapItem } from './index.d';

export default {
  name: 'c-scrollbar-bar',
  props: {
    parentRef: {
      required: true,
    },
    direction: {
      type: String,
      default: 'vertical', // æ¯åº2ä¸ªå¼ æ°´å¹³ horizontal åç´ vertical
    },
    size: {
      // thumbçå®½åº¦æé«åº¦
      type: String,
    },
    move: {
      type: String,
    },
    trigger: {
      type: String,
      default: 'hover', // hover é¼ æ ç§»å¨ä¸å»æ¾ç¤º always ä¸ç´æ¾ç¤º none ä¸æ¾ç¤º
    },
    barStyle: {
      // è®¾ç½®è½¨é
      type: Object,
      default: () => ({
        'background-color': '',
      }),
    },
    thumbStyle: {
      // è®¾ç½®æ»å
      type: Object,
      default: () => ({
        'background-color': 'rgba(0, 0, 0, 0.2)',
      }),
    },
  },
  setup(props: any) {
    const barRef = ref();
    const bar: ComputedRef<BarMapItem> = computed(() => (BAR_MAP as BarMap)[props.direction]);
    const thumbStyleObj = computed(() => ({
      ...props.thumbStyle,
      [bar.value.size]: props.size,
      transform: `translate${bar.value.axis}(${props.move})`,
    }));

    /**
     * ç¹å»è½¨é æ»å¨åºåæ»å¨å°å¯¹åºçä½ç½® å®ä½å°æ»åçä¸­å¿ç¹
     */
    const thumbRef = ref();
    function handleBarMouseDown(e: any) {
      const client = e[bar.value.client]; // ç¹å»ä½ç½®è·å®¢æ·ç«¯é¡¶é¨ææå·¦è¾¹çä½ç½®
      const wrap = barRef.value.getBoundingClientRect()[bar.value.direction]; // æ»å¨è½¨éè·é¡¶é¨ææå·¦è¾¹çä½ç½®
      const offset = Math.abs(wrap - client); // è·ç¦»åç´ ä¸æå·¦è¾¹è·çè·ç¦»
      const thumbHalf = thumbRef.value[bar.value.offset] / 2; // thumbä¸è¬çé«åº¦
      const $parentWrap = props.parentRef;
      const thumbPosPercent = ((offset - thumbHalf) * 100) / barRef.value[bar.value.offset];
      $parentWrap[bar.value.scroll] = (thumbPosPercent * $parentWrap[bar.value.scrollSize]) / 100;
    }

    const thumbDrag = ref(false);

    /**
     * æå¨è¿ç¨ä¸­
     */
    function handleThumbMove(e: any) {
      if (!thumbDrag.value) {
        return;
      }
      const client = e[bar.value.client]; // ç¹å»ä½ç½®è·å®¢æ·ç«¯é¡¶é¨ææå·¦è¾¹çä½ç½®
      const wrap = barRef.value.getBoundingClientRect()[bar.value.direction]; // æ»å¨è½¨éè·é¡¶é¨ææå·¦è¾¹çä½ç½®
      const offset = Math.abs(wrap - client); // è·ç¦»åç´ ä¸æå·¦è¾¹è·çè·ç¦»
      const thumbHalf = thumbRef.value[bar.value.offset] / 2; // thumbä¸è¬çé«åº¦
      const $parentWrap = props.parentRef;
      const thumbPosPercent = ((offset - thumbHalf) * 100) / barRef.value[bar.value.offset];
      $parentWrap[bar.value.scroll] = (thumbPosPercent * $parentWrap[bar.value.scrollSize]) / 100;
    }

    /**
     * æ¾å¼é¼ æ ï¼æå¨ç»æ
     */
    function hanldeThumbUp(event: any) {
      thumbDrag.value = false;
      if (event.target) {
        document.removeEventListener('mousemove', handleThumbMove); // æ³¨åå¨documentä¸èä¸æ¯event.targetä¸æ¯ä¸ºäºæ´æµçï¼ä¸ºäºè§£å³é¼ æ æ»å¨è¿å¿«ï¼æ»å¨æ»åè·ä¸ä¸ï¼åæªéæ¾é¼ æ æ¶ï¼å¨æ»åææ»å¨çé®é¢
        document.removeEventListener('mouseup', hanldeThumbUp);
      }
      document.onselectstart = () => null;
    }

    /**
     * ææ½å¼å§
     */
    function startDrag(event: Event) {
      thumbDrag.value = true;
      if (event.target) {
        document.addEventListener('mousemove', handleThumbMove);
        document.addEventListener('mouseup', hanldeThumbUp);
      }
      document.onselectstart = () => false;
    }

    /**
     * é¼ æ å¨æ»åthumbä¸æä¸
     */
    function handleThumbMouseDown(event: Event) {
      startDrag(event);
    }

    return {
      barRef,
      thumbRef,
      bar,
      thumbStyleObj,
      handleBarMouseDown,
      handleThumbMouseDown,
    };
  },
};
</script>

<style lang="less">
.c-scrollbar-bar {
  position: absolute;
  z-index: 1;
  border-radius: 4px;
  transition: opacity 120ms ease-out;

  .c-scrollbar-thumb {
    width: 0;
    height: 0;
    cursor: pointer;
    border-radius: inherit;
    background-color: rgba(0, 0, 0, 0.7);
    transition: 0.3 background-color;
  }

  &.c-scrollbar-vertical {
    width: 6px;
    top: 0;
    bottom: 0;
    right: 0;
    .c-scrollbar-thumb {
      width: 100%;
    }
  }

  &.c-scrollbar-horizontal {
    height: 6px;
    left: 0;
    right: 0;
    bottom: 0;
    .c-scrollbar-thumb {
      height: 100%;
    }
  }
}
</style>
